import React, { Component } from "react";
//import SkillCard from "../../components/skillCard/SkillCard.js";
import "./SkillAccordion.css";
import { Accordion, Panel } from "baseui/accordion";
import { Fade } from "react-reveal";

class SkillAccordion extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="skill-accord">
        <Accordion onChange={({ expanded }) => console.log(expanded)}>
          {this.props.sections.map((section) => {
            return (
              <Panel className="accord-panel" title={section["title"]} key={section["title"]}>
                <div className="tool-container">
                <div className="tool-bar">
                {section["skill"].slice(0,Math.ceil(section["skill"].length/2)).map((skill) => {
                  const progressStyle = {
                    width: skill.progressPercentage
                };
                  return (
                    <div className="tool"><p><span
                    className="iconify"
                    data-icon={skill.fontAwesomeClassname}
                    style={skill.style}
                    data-inline="false"
                    ></span><span style={{ color: theme.text }}>{skill.Stack}</span></p>
                    <div className="tool-meter">
                                <span style={progressStyle}></span>
                            </div>
                    </div>
                    
                  );
                })}
              </div>
              <div className="tool-bar">
                {section["skill"].slice(Math.ceil(section["skill"].length/2)).map((skill) => {
                  const progressStyle = {
                    width: skill.progressPercentage
                };
                  return (
                    <div className="tool"><p><span
                    className="iconify"
                    data-icon={skill.fontAwesomeClassname}
                    style={skill.style}
                    data-inline="false"
                    ></span><span style={{ color: theme.text }}>{skill.Stack}</span></p>
                    <div className="tool-meter">
                                <span style={progressStyle}></span>
                            </div>
                    </div>
                    
                  );
                })}
              </div>
              </div>
              </Panel>
            );
          })}
        </Accordion>
      </div>
    );
  }
}

export default SkillAccordion;
