import React from "react";
import "./skillLanguage.css";
import { sLanguage } from "../../portfolio";
import { Fade } from "react-reveal";
import YumaDeskImg from "./YumaDeskImg"

export default function SkillLanguage(props) {
    const theme = props.theme;
    if (sLanguage.viewSkillBars) {
        return (
            <Fade bottom duration={2000} distance="40px">
             <div className="lskills-image">
                    <YumaDeskImg theme={theme}/>
                    {/*<img alt="Skills" src={require("../../assests/images/YumaGenomics.svg")} />*/}
                    
            </div>
            <div className="lskills-heading">
                <h1>Coding Languages</h1>
            </div>
            <div className="lskills-container">
            <Fade left duration={1500}>
                <div className="lskills-bar">
                    
                    {sLanguage.experience.slice(0, Math.ceil(sLanguage.experience.length/2)).map((exp) => {
                        const progressStyle = {
                            width: exp.progressPercentage
                        };
                        return (
                        <div className="lskill">
                            <p> <span
                            className="iconify"
                            data-icon={exp.fontAwesomeClassname}
                            style={exp.style}
                            data-inline="false"
                            ></span> {exp.Stack}</p>
                            <div className="lmeter">
                                <span style={progressStyle}></span>
                            </div>
                        </div>
                        );
                    })}
                </div>
            </Fade>

            <Fade right duration={1500}>
                <div className="lskills-bar">
                    {sLanguage.experience.slice(Math.ceil(sLanguage.experience.length/2)).map((exp) => {
                        const progressStyle = {
                            width: exp.progressPercentage
                        };
                        return (
                        <div className="lskill">
                            <p>
                            <span
                            className="iconify"
                            data-icon={exp.fontAwesomeClassname}
                            style={exp.style}
                            data-inline="false"
                            ></span>{exp.Stack}</p>
                            <div className="lmeter">
                                <span style={progressStyle}></span>
                            </div>
                        </div>
                        );
                    })}
                </div>
            </Fade>

            </div>
            </Fade>
        );
    }
    return null;
}